@use 'variables'as v;

@mixin phone {
    @media (min-width: v.$phone) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}

@mixin desktopXL {
    @media (min-width: v.$desktopXL) {
        @content;
    }
}

//*Counters

@mixin phoneC {
    @media(max-width: 755px) {
        @content;
    }
}

@mixin tabletC {
    @media(max-width:1268px) {
        @content;
    }
}