html {
  font-size: 62.5%;
  box-sizing: border-box;
  font-family: "Kanit", sans-serif;
  transition: all 0.5s ease-in-out;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-size: 1.6rem;
}

.content {
  width: 90%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1,
h2,
h3 {
  margin: 0;
}

ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
}

.header {
  background-image: url(../../../img/banner.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 10% 70%;
}
@media (min-width: 768px) {
  .header {
    height: 60rem;
  }
}
.header .navegation {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 3rem;
  flex-direction: column;
}
@media (min-width: 768px) {
  .header .navegation {
    flex-direction: row;
    align-items: center;
  }
}
.header .navegation .header__logo img {
  width: 20rem;
  height: 10rem;
}
@media (min-width: 768px) {
  .header .navegation .mobile-menu {
    display: none;
  }
}
.header .navegation .mobile-menu i {
  color: #ffffff;
  font-size: 5rem;
  margin: 2.5rem;
}
.header .navegation .show_nav {
  display: flex;
  visibility: visible;
  opacity: 1;
}
.header .text-principal {
  text-align: center;
  color: white;
  font-size: 4rem;
  text-transform: uppercase;
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .header .text-principal {
    margin-top: 10rem;
  }
}

.bar-navegation {
  display: none;
  gap: 3.5rem;
  flex-direction: column;
  text-align: center;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear;
}
@media (min-width: 768px) {
  .bar-navegation {
    display: flex;
    flex-direction: row;
    visibility: visible;
    opacity: 1;
  }
}
.bar-navegation a {
  color: #ffffff;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 1.85rem;
  transform: scale(1.12);
  transition: 0.6s transform;
}
.bar-navegation a:hover {
  color: #830006;
  transition: 0.5s color;
}

.information-mision_vision {
  display: grid;
  grid-template-columns: none;
  color: rgb(252, 252, 252);
  margin: 6rem 0;
  gap: 6rem;
}
@media (min-width: 768px) {
  .information-mision_vision {
    grid-template-columns: repeat(2, 1fr);
  }
}
.information-mision_vision .mision,
.information-mision_vision .vision {
  padding: 8rem 5rem;
  font-size: 2rem;
}
.information-mision_vision .mision {
  background-color: #830006;
}
.information-mision_vision .vision {
  background-color: #424244;
}
.information-mision_vision h2 {
  text-transform: uppercase;
  text-align: center;
}
.information-mision_vision p {
  text-align: justify;
}

.service__title {
  text-align: center;
  text-transform: uppercase;
  font-size: 5rem;
  margin: 5rem;
  color: #830006;
}

.services {
  display: grid;
  gap: 2.4rem;
  grid-template-rows: none;
  margin-bottom: 10rem;
}
@media (min-width: 768px) {
  .services {
    grid-template-columns: repeat(5, 1fr);
  }
}
@media (max-width: 1268px) {
  .services {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (max-width: 755px) {
  .services {
    grid-template-columns: repeat(1, 1fr);
  }
}
.services .service_1 {
  background-image: url(../../../img/1.png);
}
.services .service_2 {
  background-image: url(../../../img/2.png);
}
.services .service_3 {
  background-image: url(../../../img/3.png);
}
.services .service_4 {
  background-image: url(../../../img/4.png);
}
.services .service_5 {
  background-image: url(../../../img/5.png);
}
.services .service_6 {
  background-image: url(../../../img/6.png);
}
.services .service_7 {
  background-image: url(../../../img/7.png);
}
.services .service_8 {
  background-image: url(../../../img/8.png);
}
.services .service_9 {
  background-image: url(../../../img/9.png);
}
.services .service_1,
.services .service_2,
.services .service_3,
.services .service_4,
.services .service_5,
.services .service_6,
.services .service_7,
.services .service_8,
.services .service_9 {
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  width: 100%;
  height: 30rem;
  position: relative;
  background-blend-mode: darken;
  background-color: rgba(0, 0, 0, 0.6);
}
.services .service_1 h2,
.services .service_2 h2,
.services .service_3 h2,
.services .service_4 h2,
.services .service_5 h2,
.services .service_6 h2,
.services .service_7 h2,
.services .service_8 h2,
.services .service_9 h2 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-transform: uppercase;
  font-size: 2.8rem;
  text-align: center;
  color: #ffffff;
}
.services .service_1:hover,
.services .service_2:hover,
.services .service_3:hover,
.services .service_4:hover,
.services .service_5:hover,
.services .service_6:hover,
.services .service_7:hover,
.services .service_8:hover,
.services .service_9:hover {
  cursor: pointer;
  transform: scale(1.12);
  transition: 0.6s transform;
}

.section_form .section_form__contact {
  padding: 0 2rem 2rem 2rem;
}
.section_form .section_form__contact .form__title {
  text-align: center;
  text-transform: uppercase;
  font-weight: 700;
  margin: 1.5rem 0;
  font-size: 4.5rem;
  color: #830006;
}
.section_form .section_form__contact .bg-contact {
  background-image: url(../../../img/contac.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 80% center;
  height: 40rem;
}
.section_form .section_form__contact .form {
  padding: 5rem;
  background-color: #ffffff;
  border-radius: 1rem;
  margin: -5rem auto 0 auto;
  width: 95%;
}
.section_form .section_form__contact .form .camp {
  display: flex;
  margin-bottom: 1.5rem;
  color: #161616;
  font-size: 1.8rem;
  flex-direction: column;
}
@media (min-width: 768px) {
  .section_form .section_form__contact .form .camp {
    flex-direction: row;
  }
}
.section_form .section_form__contact .form .camp input,
.section_form .section_form__contact .form .camp textarea {
  border: 1px solid #e1e1e1;
  width: 100%;
}
.section_form .section_form__contact .form .camp textarea {
  height: 20rem;
}
.section_form .section_form__contact .form .camp label {
  flex: 0 0 0;
}
@media (min-width: 768px) {
  .section_form .section_form__contact .form .camp label {
    text-align: right;
    padding: 1rem 2rem 1rem 0;
    flex: 0 0 9rem;
  }
}
.section_form .section_form__contact .form .botons {
  text-align: center;
}
@media (min-width: 768px) {
  .section_form .section_form__contact .form .botons {
    text-align: left;
    width: 100%;
  }
}
.section_form .section_form__contact .form .botons .boton {
  color: rgba(128, 128, 128, 0.7);
  text-align: center;
  padding: 1rem 3rem;
  font-size: 1.8rem;
  text-transform: uppercase;
  font-weight: 700;
  border: none;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .section_form .section_form__contact .form .botons .boton {
    margin-left: 8.9rem;
  }
}
.section_form .section_form__contact .form .botons .boton:hover {
  cursor: pointer;
}
.section_form .section_form__contact .form .botons .wsp {
  margin-top: 1rem;
  color: #ffffff;
  background-color: #35BC48;
  font-size: 2rem;
}
.section_form .section_form__contact .form .botons .wsp span {
  font-family: "Kanit", sans-serif;
  font-weight: 700;
}

.footer {
  background-color: #424244;
  padding: 2rem;
  color: #ffffff;
  text-align: center;
}

.fixed {
  position: fixed;
  background-color: #35BC48;
  color: #ffffff;
  border-radius: 50%;
  padding: 1.5rem;
  top: 80%;
  right: 0%;
  font-size: 5rem;
  margin-right: 0.8rem;
}

.fixed_body {
  overflow: hidden;
}

.overlay {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay .content__overlay {
  width: 100%;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.3);
  place-items: center;
  display: grid;
  padding: 3rem;
  gap: 2rem;
}
@media (min-width: 768px) {
  .overlay .content__overlay {
    width: 52%;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }
}
.overlay .content__overlay .overlay_title_service,
.overlay .content__overlay .overlay__text__service {
  margin: 0;
}
.overlay .content__overlay .overlay__image__service {
  grid-row: 1/3;
  width: 100%;
  text-align: center;
}
.overlay .content__overlay .overlay__image__service img {
  width: 80%;
  border-radius: 1rem;
}
.overlay .content__overlay .overlay_title_service {
  text-transform: uppercase;
  color: #830006;
  text-align: center;
}
.overlay .content__overlay .overlay__text__service {
  margin-top: 0;
  text-align: justify;
}
@media (min-width: 768px) {
  .overlay .content__overlay .overlay__text__service {
    margin-top: -8rem;
  }
}

.btn-cerrar {
  font-weight: bold;
  background-color: #ffffff;
  color: #161616;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  display: grid;
  place-items: center;
  margin-top: -10rem;
}
@media (min-width: 768px) {
  .btn-cerrar {
    margin-top: -40rem;
  }
}
.btn-cerrar:hover {
  cursor: pointer;
}